*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.container-main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 768px) {
  .container-main {
    padding: 0 40px;
  }
}

/* Please do not use this import in production. You could download this font from here https://fontstorage.com/font/bebasneue */
/*
	Bebas Neue, 100, 200, 300, 400, 700
*/
@font-face {
  font-family: 'Bebas Neue';
  src: url("../fonts/BebasNeue Bold.otf") format("opentype"), url("../fonts/BebasNeue Bold.ttf") format("truetype");
  font-weight: bold;
}

body {
  font-family: Bebas Neue, Arial, Helvetica, "Lucida Sans Unicode", "Lucida Grande", Verdana, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  line-height: 1;
  margin: 0;
  text-transform: uppercase;
  font-weight: bold;
}

h1, h2, h3, h4, h5, h6, p, span {
  color: white;
}

h1 {
  font-size: 50px;
}

h2 {
  font-size: 43px;
}

h3 {
  font-size: 28px;
}

h4 {
  font-size: 18px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo__text {
  margin-left: 15px;
}

.footer .logo__text {
  display: none;
}

.header {
  background-color: black;
  padding-top: 20px;
  padding-bottom: 56px;
}

.header__container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header__text {
  margin-top: 20px;
  text-align: left;
}

.header__text_title {
  margin-bottom: 5px;
}

@media screen and (min-width: 640px) {
  .header__container {
    flex-direction: row;
  }
  .header__text {
    margin-top: 0px;
    text-align: right;
  }
}

.hero {
  width: 100%;
  height: 610px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
}

.hero__title {
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 115px;
  left: 0;
}

.estamos {
  background-color: black;
  padding-top: 73px;
  padding-bottom: 83px;
  text-align: center;
}

.white {
  background-color: white;
  height: 295px;
}

.footer {
  background-color: black;
  padding-top: 150px;
  padding-bottom: 120px;
}

.footer__container {
  display: flex;
  justify-content: center;
}
